.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.banner{
  width: 100%;
  color: antiquewhite;  
  position: fixed;
  text-align: center;
}

.main-display {
  display: flex;  
}

.hero-title {
  font-size: 2em;
  font-weight: 500;
  color: antiquewhite;  
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #404041; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
  position: fixed;
  left: 47%;
  top: 200px;
} 

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.btn-groups {
  background-color: rgb(85, 87, 87); 
  border: 1px solid rgb(53, 53, 53); 
  font-size: 1em;
  color: white; 
  text-align: left; 
  cursor: pointer; 
  width: 10em;
  display: flex;
  border-radius: 25px;      
}

.grid-item-hero {
  color: white;
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  top: 100px;
  text-align: center;
  width: 85%;
}

.grid-item-player {
  color: white;
  position: fixed;
  right: 30px;    
  top: 70px;
}

.hero-text{
  font-size: 1em;
}

.player-text{   
  font-size: 1.2em;  
  text-align: center; 
  color: antiquewhite;  
}

tbody {
  text-align: right;
}

#first {
  color: gold;
}

#second {
  color: silver;  
}

#third {
  color: rgb(176, 141, 87);
}